<template>
    <div class="v-container">
        <!--  -->
        <el-breadcrumb separator=">">
            <el-breadcrumb-item :to="{ name: 'setting' }"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name: 'serverManage' }"></el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name: 'serverAgentManage' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <hr>
        <div id="myChart" style="{width:99%;height:780px;}">
        </div>

        <el-popover :popData="popData" :style="{left:popData.left,top:popData.top,position:'absolute',zIndex:1}" width="244" v-model="popData.visible">
            <el-row>
                <el-button size="mini" type="warning" @click="onUnlinkedNode"></el-button>
                <el-button size="mini" type="primary" @click="addNode"></el-button>
                <el-button size="mini" type="primary" @click="popData.visible = false"></el-button>
            </el-row>
        </el-popover>


        <el-dialog
                title=""
                :visible.sync="nodeData.visible"
                width="30%"
                :modal="false"
                custom-class="v-right-dialog"
                @close="onClose(null,'nodeSearchFormRef')"
        >

            <el-form ref="nodeSearchFormRef" :inline="true" :model="nodeSearchForm" size="mini">
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-input v-model="nodeData.name"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="">
                            <el-input v-model="nodeData.name"></el-input>
                        </el-form-item>
                    </el-col>
                    <hr>
                    <el-col :span="12">
                        <el-form-item label="" prop="server_name">
                            <el-input v-model="nodeSearchForm.server_name" placeholder=""></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="ip" prop="server_inner_ip">
                            <el-input v-model="nodeSearchForm.server_inner_ip" placeholder=""></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :offset="6" :span="18">
                        <el-button size="mini" type="primary" @click="onSearch"><i class="iconfont icon-chaxun"></i> </el-button>
                        <el-button size="mini" type="primary" @click="onReset('nodeSearchFormRef')"><i class="iconfont icon-chongzhi"></i> </el-button>
                        <el-button size="mini" type="primary" @click="onAddNode"><i class="iconfont icon-add"></i></el-button>
                    </el-col>
                </el-row>
                <el-table
                        ref="serverInfoListRef"
                        :data="serverInfoList"
                        tooltip-effect="dark"
                        style="width: 100%"
                        size="mini"
                        height="520px"
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            prop="server_name"
                            label=""
                            width="120">
                        <!--<template slot-scope="scope">{{ scope.row.date }}</template>-->
                    </el-table-column>
                    <el-table-column
                            prop="server_inner_ip"
                            label="ip"
                            width="140">
                    </el-table-column>
                    <el-table-column
                            prop="server_outer_ip"
                            label="ip"
                            show-overflow-tooltip>
                    </el-table-column>
                </el-table>
                <div style="margin-top: 3px">
                    <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :total="nodeSearchForm.pageCount"
                            :current-page="nodeSearchForm.pageNum"
                            :page-size="nodeSearchForm.pageSize"
                            :page-sizes="[12, 13, 50, 100]"
                            small
                            layout="sizes,prev, pager, next,jumper"
                    >
                    </el-pagination>
                    <!--<el-button @click="toggleSelection([tableData3[1], tableData3[2]])"></el-button>
                    <el-button @click="toggleSelection()"></el-button>-->
                </div>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
    // 
    let echarts = require('echarts/lib/echarts');
    // 
    require("echarts/lib/chart/tree");
    // title
    require('echarts/lib/component/tooltip');
    require('echarts/lib/component/title');
    export default {
        name: 'agentConf',
        data() {
            return {
                popData: //hover 
                    {
                        left: '0px',
                        top: '0px',
                        visible: false
                    },
                nodeData: // 
                    {
                        name: '',
                        visible: false,
                    },
                nodeSearchForm:  // 
                    {
                        server_name: '',
                        server_inner_ip: '',
                        pageNum: 1,
                        pageSize: 8,
                        pageCount: 2000
                    },
                serverInfoList: [], //
                multipleSelection: [],// 
                chartData: {},// 
                agent_id: null,
                agent_server_id: null
            }
        },
        mounted() {
            this.load_tree();
        },
        computed: {},
        methods: {
            handleSizeChange(val) //
            {
                this.nodeSearchForm.pageSize = val;
                this.search();
            },
            handleCurrentChange(val) //
            {
                this.nodeSearchForm.pageNum = val;
                this.search();
            },
            onReset(ref) // 
            {
                this.$refs[ref].resetFields();
                this.search();
            },

            onSearch() {
                this.nodeSearchForm.pageNum = 1;
                this.search();
            },
            async r_linked_server_to_agent(par) {
                let R = new this.$http();
                let {data} = await R.post(par, 'OAMP_linked_server_to_agent');
                if (data != null && data !== 'null')
                    return data;
            },
            async r_unlinked_server_to_agent(par) {
                let R = new this.$http();
                let {data} = await R.post(par, 'OAMP_unlinked_server_to_agent');
                if (data != null && data !== 'null')
                    return data;
            },
            async r_search_list(searchData) {
                let R = new this.$http();
                let {data} = await R.post(searchData, 'OAMP_search_agent_server_list');
                this.nodeSearchForm.pageCount = data.pageCount;
                this.serverInfoList = data.data
            },
            async r_search_agent_conf_tree_data(searchData) {
                let R = new this.$http();
                let {data} = await R.post(searchData, 'OAMP_search_agent_conf_tree_data');
                if (data != null && data !== 'null')
                    return data;
            },
            handleSelectionChange(val) // 
            {
                this.multipleSelection = val;
            },
            search() // 
            {
                let searchData = this.nodeSearchForm;
                this.r_search_list(searchData)
            },
            async load_tree() {
                if (this.$refs.nodeSearchFormRef)
                    this.$refs.nodeSearchFormRef.resetFields();
                let agent_id = this.$route.params.serverId;
                this.chartData = await this.r_search_agent_conf_tree_data({agent_id: agent_id});
                // console.log(this.chartData)
                // 
                if (this.chartData == null) {
                    this.$message.info('')
                } else {
                    this.drawPic()
                }
            },
            addNode() // echart 
            {
                this.popData.visible = false;
                this.nodeData.visible = true;
                this.multipleSelection = []; //
                if (this.$refs.serverInfoListRef)
                    this.$refs.serverInfoListRef.clearSelection(); // 
                this.search(); // 
            },
            async onAddNode()  // 
            {
                if (this.multipleSelection && (this.multipleSelection.length) > 0) {
                    let par = {
                        "agent_id": this.agent_id,
                        "agent_server_id": this.agent_server_id,
                        "server_list": this.multipleSelection,
                    };
                    let data = await this.r_linked_server_to_agent(par);
                    if (data.success) {
                        this.$message.success('');
                        this.popData.visible = false;
                        this.load_tree();
                        this.search();
                    }
                    else
                        this.$message.error('');
                } else {
                    this.$message.info('')
                }
            },
            async onUnlinkedNode() // 
            {
                let par = {"_id": this.agent_server_id};
                let data = await this.r_unlinked_server_to_agent(par);
                if (data.success) {
                    this.$message.success('');
                    this.popData.visible = false;
                    this.load_tree();
                }
                else
                    this.$message.error('');
            },
            drawPic() {
                let data = this.chartData;
                let myChart = echarts.init(document.getElementById('myChart'))
                myChart.hideLoading();
                echarts.util.each(data.children, function (datum, index) {
                    index % 2 === 0 && (datum.collapsed = true);
                });
                let _this = this;
                myChart.on('mouseover', function (param) {
                    _this.popData.left = param.event.offsetX - 60 + 'px';
                    _this.popData.top = param.event.offsetY + 10 + 'px';
                    _this.popData.visible = true;
                    _this.agent_server_id = param.data.id;
                    _this.agent_id = _this.$route.params.serverId;
                });
                myChart.setOption({
                    title: {
                        text: ''
                    },
                    tooltip: {
                        trigger: 'item',
                        triggerOn: 'mousemove'
                    },
                    series: [
                        {
                            type: 'tree',
                            data: [data],
                            top: '25%',
                            left: '15%',
                            bottom: '35%',
                            right: '25%',
                            symbolSize: 20,
                            label: {
                                normal: {
                                    position: 'left',
                                    verticalAlign: 'middle',
                                    align: 'right',
                                    fontSize: 16
                                }
                            },
                            leaves: {
                                label: {
                                    normal: {
                                        position: 'right',
                                        verticalAlign: 'middle',
                                        align: 'left'
                                    }
                                }
                            },
                            lineStyle: {
                                color: '#3366ff',
                            },
                            orient: 'TB',
                            expandAndCollapse: true,
                            animationDuration: 550,
                            animationDurationUpdate: 750
                        }
                    ]
                });
            }

        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /* */
    @keyframes dialog-fade-in {
        0% {
            transform: translate3d(100%, 0, 0);
            opacity: 0;
        }
        100% {
            transform: translate3d(0, 0, 0);
            opacity: 1;
        }
    }

    @keyframes dialog-fade-out {
        0% {
            transform: translate3d(0, 0, 0);
            opacity: 1;
        }
        100% {
            transform: translate3d(100%, 0, 0);
            opacity: 0;
        }
    }
</style>




